import request from '@/utils/request'

const baseUrl = '/api/admin/extField'

const extFieldApi = {}

extFieldApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

extFieldApi.page = (params) => {
  return request({
    url: `${baseUrl}/page-plus`,
    params: params,
    method: 'get'
  })
}

extFieldApi.get = extFieldId => {
  return request({
    url: `${baseUrl}/${extFieldId}`,
    method: 'get'
  })
}

extFieldApi.getExtFields = params => {
  return request({
    url: `${baseUrl}/getByCategoryIds`,
    params: params,
    method: 'get'
  })
}

extFieldApi.create = extField => {
  return request({
    url: `${baseUrl}`,
    data: extField,
    method: 'post'
  })
}

extFieldApi.update = extField => {
  return request({
    url: `${baseUrl}`,
    data: extField,
    method: 'put'
  })
}

extFieldApi.delete = extFieldId => {
  return request({
    url: `${baseUrl}/${extFieldId}`,
    method: 'delete'
  })
}

export default extFieldApi
