<template>
  <div>
    <el-row :gutter="20">
      <el-col class="mb20" :xl="5" :lg="6" :md="10" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>选择栏目</span>
          </div>
          <category-tree @node-click="handleNodeClick"></category-tree>
        </el-card>
      </el-col>
      <el-col class="mb20" :xl="19" :lg="18" :md="14" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>字段列表</span>
          </div>
          <div>
            <el-form :inline="true">
              <el-form-item label="字段名称">
                <el-input v-model="queryParams.title" placeholder="字段名称"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="getExtField()">查询</el-button>
                <el-button type="primary" @click="showForm(null)">新增</el-button>
              </el-form-item>
            </el-form>
          </div>
          <div style="padding:20px;">
            <el-table
              :data="fields"
              style="margin-bottom:20px;"
              v-loading="tableLoading"
              element-loading-background="rgba(0, 0, 0, 0.3)"
              element-loading-text="数据正在加载中"
              element-loading-spinner="el-icon-loading"
            >
              <el-table-column prop="id" label="ID" width="60"></el-table-column>
              <el-table-column prop="name" label="名称" width="90"></el-table-column>
              <el-table-column prop="field" label="字段"></el-table-column>
              <el-table-column prop="categoryName" label="栏目"></el-table-column>
              <el-table-column prop="size" label="长度"></el-table-column>
              <el-table-column prop="value" label="字段值" show-overflow-tooltip></el-table-column>
              <el-table-column label="字段所属">
                <template slot-scope="scope">{{scope.row.struct=="content"?"内容":"栏目"}}</template>
              </el-table-column>
              <el-table-column prop="createTime" label="创建时间" width="180"></el-table-column>
              <el-table-column label="操作" width="150">
                <template slot-scope="scope">
                  <el-button type="text" @click="showForm(scope.row)">修改</el-button>
                  <el-popconfirm
                    title="确定删除本条数据吗？删除后不可恢复。"
                    style="margin-left:10px;"
                    cancelButtonType="default"
                    @onConfirm="del(scope.row)"
                  >
                    <el-button type="text" slot="reference">删除</el-button>
                  </el-popconfirm>
                </template>
              </el-table-column>
            </el-table>
            <pagination
              @sizeChange="handleSizeChange"
              @currentChange="handleCurrentChange"
              :pageNum="queryParams.pageNum"
              :pageSize="queryParams.pageSize"
              :total="total"
            ></pagination>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="新增字段" width="500px" :visible.sync="showAddForm">
      <el-form :model="editParams" label-position="right" label-width="80px">
        <el-form-item label="名称">
          <el-input v-model="editParams.name" autocomplete="off" placeholder="名称：建议2-4字以内"></el-input>
        </el-form-item>
        <el-form-item label="字段">
          <el-input
            v-model="editParams.field"
            :disabled="editParams.id != null"
            autocomplete="off"
            placeholder="以字母开头，下划线分隔，例：price"
          ></el-input>
        </el-form-item>
        <el-form-item label="类型">
          <el-select v-model="editParams.type" :disabled="editParams.id != null" placeholder="请选类型">
            <el-option label="单行文本" value="input"></el-option>
            <el-option label="多行文本" value="textarea"></el-option>
            <el-option label="下拉列表" value="select"></el-option>
            <el-option label="日期选择" value="date"></el-option>
            <el-option label="单选按钮" value="radio"></el-option>
            <el-option label="多选按钮" value="checkbox"></el-option>
            <el-option label="图片上传" value="image"></el-option>
            <el-option label="附件上传" value="file"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="长度">
          <el-input
            v-model="editParams.size"
            autocomplete="off"
            :disabled="editParams.id != null"
            placeholder="字段长度"
          ></el-input>
        </el-form-item>
        <el-form-item label="字段值">
          <el-input v-model="editParams.value" autocomplete="off" placeholder="只在单选、多选、下拉选择时有效"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="editParams.description" autocomplete="off" placeholder="字段描述"></el-input>
        </el-form-item>
        <el-form-item label="字段所属">
          <el-radio-group v-model="editParams.struct">
            <el-radio label="content">内容</el-radio>
            <el-radio label="category">栏目</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showAddForm = false">取消</el-button>
        <el-button type="primary" @click="saveField">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import CategoryTree from "@/views/components/category-tree";
import Pagination from "@/components/pagination";
import extFieldApi from "@/api/extFieldApi";
export default {
  components: {
    CategoryTree,
    Pagination
  },
  data() {
    return {
      tableLoading: false,
      fields: [],
      queryParams: {
        pageNum: 1,
        pageSize: 10
      },
      total: 0,
      showAddForm: false,
      editParams: {
        size: 255,
        type: "input",
        struct: "content"
      }
    };
  },
  created() {
    this.getExtField();
  },
  methods: {
    getExtField() {
      if (this.queryParams.name == "") {
        this.queryParams.name = null;
      }
      this.tableLoading = true;
      extFieldApi.page(this.queryParams).then(res => {
        this.tableLoading = false;
        this.fields = res.data.records;
        this.total = res.data.total;
      });
    },
    handleNodeClick(node) {
      this.queryParams.categoryId = node.id;
      this.editParams.categoryId = node.id;
      this.getExtField();
    },
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getExtField();
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getExtField();
    },
    showForm(data) {
      if (data) {
        this.editParams = data;
      }
      if (!this.editParams.categoryId) {
        this.$message.warning("请选择栏目分类");
        return false;
      }
      this.showAddForm = true;
    },
    saveField() {
      if (!this.editParams.name) {
        this.$message.warning("请填写名称");
        return false;
      }
      if (!this.editParams.field) {
        this.$message.warning("请填写字段");
        return false;
      }
      if (!this.editParams.size) {
        this.$message.warning("请填写长度");
        return false;
      }
      if (
        (this.editParams.type == "select" ||
          this.editParams.type == "radio" ||
          this.editParams.type == "checkbox") &&
        !this.editParams.value
      ) {
        this.$message.warning("请填写字段值");
        return false;
      }
      if (this.editParams.id) {
        extFieldApi.update(this.editParams).then(res => {
          this.$message.success("保存成功");
          this.reset();
          this.showAddForm = false;
          this.getExtField();
        });
      } else {
        extFieldApi.create(this.editParams).then(res => {
          this.$message.success("保存成功");
          this.reset();
          this.showAddForm = false;
          this.getExtField();
        });
      }
    },
    del(row) {
      extFieldApi.delete(row.id).then(res => {
        this.$message.success("删除成功");
        this.getExtField();
      });
    },
    reset() {
      this.editParams.id = null;
      this.editParams.size = 255;
      this.editParams.type = "input";
      this.editParams.name = "";
      this.editParams.field = "";
    }
  }
};
</script>

